.about__container{
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}

.about__me{
    width: 55%;
    aspect-ratio: 1/1;
    border-radius: 1rem;
    display: flex;
    justify-content:center;
    align-items: center;
    margin-left: 23%;
}


.about__me-image {
    border-radius:2rem;
    overflow: hidden;
    transform:rotate(10deg);

}
about__me-image:hover{
    transform: rotate(0);
}
h3{
    display: flex;
    justify-content: center;
}


.about__cards{
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center; 
}
.about__card{
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    transition: var(--transition);
    
}

.about__card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__icon{
    color: var(--color-primary);
    font-size: 1.5rem;
    margin-bottom: 1rem;
}

.articles{
    display: grid;
    grid-template-columns: 2fr 2fr 2fr;
    gap: 3%;
    height: 300px;
    justify-content: center;

}

.about__card h5{
    font-size: 0.95rem;
}
.about__card small {
    font-size: 0.7rem;
    color: var(--color-light);

}
.about__content p{
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}


@media screen and (max-width: 1024px) {
    .about__container {
        grid-template-columns: 1fr;
        gap: 0;
    }
    .about__me {
        width: 50%;
        margin: 2rem auto 4rem;

    }
    .articles {
        width: 50%;
        margin: 2rem auto 4rem;

    }
    .about__content p {
        margin: 1rem 0 1.5rem;
    }
 }
 
  
  @media screen and (max-width: 600px) {
    .about__me{
        width: 65%;
        margin: 0 auto 3rem;
    }
    .articles{
        width: 65%;
        margin: 0 auto 3rem;
    }
    .about__cards{
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }
  .about__content{
    text-align: center;

  }
  .about__content p{
    margin: 1.5rem 0;
  }
  }

.articles{
    display: flex;
    justify-content: column;
}

